import request from '../utils/http'

//查找全部/部分api
export function getDeviceTypeData (payload) {
  return request({
    url: 'deviceType/get',
    method: 'GET',
    params:payload
  })
}
export function getDeviceData (payload) {
    return request({
      url: 'device/get',
      method: 'GET',
      params:payload
    })
  }



