<template>
    <div class="deviceType-body">
      <div class="deviceType-above">
        <div class="deviceType-above-body">
          <span> 设备类型 </span>
          <div class="deviceType-above-body-input">
            <el-input
              v-model="input"
              placeholder="请输入角色名称"
              maxlength="30px"
            />
          </div>
          <div class="deviceType-above-body-button">
            <el-button type="primary">查询</el-button>
          </div>
        </div>
      </div>
      <div class="deviceType-under">
        <div class="deviceType-under-body">
          <div class="deviceType-under-body-button">
            <el-button type="primary">新增</el-button>
          </div>
          <div class="deviceType-under-body-table">
            <el-table :data="roleTable" style="width: 100%">
              <el-table-column prop="name" label="设备名称" width="180" />
              <el-table-column prop="createTime" label="创建时间" width="180" />
              <el-table-column prop="updateTime" label="更新时间" />
              <el-table-column prop="_id" label="设备类型id" />
              <el-table-column prop="desc" label="描述" />
              <el-table-column prop="deviceTypeId" label="编码" />
              <el-table-column prop="img" label="设备图片" >
                <template>
                    <img src="" alt="加载中...">
                </template>
              </el-table-column>
              <el-table-column prop="status" label="状态" />
              <el-table-column prop="isCustom" label="自定义" />
              <el-table-column label="操作" >
                <template #default="scope">
                <span style="font-size: 10px; color: #4d70ff; cursor: pointer" @click="getScope(scope.$index)"
                  >操作
                </span>
                <span
                  style="
                    font-size: 10px;
                    margin-left: 10px;
                    color: #4d70ff;
                    cursor: pointer;
                  "
                  @click="deleRow(scope.$index)"
                  >删除</span
                >
              </template>
              </el-table-column>
            </el-table>
          </div>
          <div class="deviceType-under-body-pagination">
            <el-pagination
              background
              layout="prev, pager, next"
              :total="total"
              @current-change="paging"
            />
          </div>
        </div>
      </div>
    </div>
  </template>
    
    <script>
  import { ref, reactive, onMounted } from "vue";
  import { getDeviceData } from "@/api/configurationCenter.js";
  export default {
    setup() {
      let input = ref("");
      let value = ref("");
      const payload = {
        currPage: "1",
        pageSize: "4",
      };
      const options = [
        {
          value: "Option1",
          label: "Option1",
        },
        {
          value: "Option2",
          label: "Option2",
        },
        {
          value: "Option3",
          label: "Option3",
        },
        {
          value: "Option4",
          label: "Option4",
        },
        {
          value: "Option5",
          label: "Option5",
        },
      ];
      let roleTable = ref([""]);
      let total = ref("0");
      function getDeviceData1(payload) {
        getDeviceData(payload).then((res) => {
          console.log(res);
          roleTable.value = res.data.data.pageData;
          total.value = Number(res.data.data.total);
        });
      }
      function paging(val) {
        let payload2 = reactive({
          currPage: val,
          pageSize: "4",
        });
        getDeviceData(payload2).then((res) => {
            roleTable.value = res.data.data.pageData;
        });
      }
      onMounted(() => {
        getDeviceData1(payload);
      });
      return {
        input,
        value,
        options,
        roleTable,
        payload,
        total,
        getDeviceData1,
        paging,
      };
    },
  };
  </script>
    
    <style scoped>
  .deviceType-body {
    width: 100%;
    height: 100%;
  }
  .deviceType-above {
    position: absolute;
    width: 85%;
    height: 13%;
    background-color: white;
    margin-top: -2%;
  }
  .deviceType-above-body {
    font-size: 15px;
    padding: 3%;
    margin-left: 3%;
    display: flex;
  }
  .deviceType-above-body-input {
    display: inline;
    width: 20%;
    margin-left: 1%;
    margin-top: -0.3%;
  }
  .deviceType-above-body-button {
    margin-left: 1%;
    margin-top: -0.3%;
  }
  .deviceType-under {
    position: absolute;
    background-color: white;
    width: 85%;
    height: 70%;
    margin-top: 5%;
  }
  .deviceType-under-body {
    display: flex;
    padding: 1%;
  }
  .deviceType-under-body-button {
    display: inline;
  }
  .deviceType-under-body-table {
    margin-left: -5%;
    padding-top: 4%;
    width: 100%;
    height: 80%;
    max-height: 80%;
    overflow: hidden;
  }
  .deviceType-under-body-pagination {
    display: inline;
    margin-top: 37%;
    margin-left: -65%;
  }
  </style>